<template>
  <v-container id="dashboard-view" fluid tag="section">
    <v-dialog
      v-model="UploadDialogue"
      transition="dialog-top-transition"
      max-width="1000"
    >
      <v-card max-width="1000">
        <v-toolbar color="primary" dark>
          <v-icon>mdi mdi-warning </v-icon>
          <h3 class="center">Uploading Your Campaign</h3>
          <v-btn
            icon
            absolute
            right
            outlined
            class="float-right"
            color="white"
            @click="reloadPage()"
          >
            <v-icon>mdi mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <div class="container" v-if="showLoader == true">
          <div class="loading-bar">
            <div class="percent" :style="{ width: loaderWidth + '%' }"></div>
          </div>
        </div>
        <v-card-actions class="justify-center">
          <v-btn color="primary">
            <h4 class="center" color="white">
              {{ loaderWidth + "%" }}
            </h4>

            <v-icon>mdi-check-outline</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-row justify="center" align="center">
      <v-col cols="1">
        <v-btn color="primary" @click="dialog = true">
          <v-icon class="pa-2">
            mdi-pencil
          </v-icon>
          Create Campaign
        </v-btn>
      </v-col>
    </v-row>

    <div>
      <v-row justify="center">
        <v-col cols="12" md="12" class="mx-auto">
          <div @click="activeCampaign = !activeCampaign">
            <div class="display-1 font-weight-bold float-left cursor">
              Active & Approved campaigns
              <v-icon v-if="activeCampaign">
                mdi mdi-chevron-up
              </v-icon>
              <v-icon v-else>
                mdi mdi-chevron-down
              </v-icon>
            </div>
            <div class="float-right body-2">
              {{ activeCampaigns.length }} active campaign
            </div>
          </div>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col md="12">
          <transition
            name="fade"
            v-for="(campaign, index) in unApprovedCampaigns"
            :key="index"
          >
            <v-card
              v-if="activeCampaign"
              class="mx-auto"
              min-width="344"
              outlined
              transition="fade"
            >
              <v-list-item three-line>
                <v-list-item-avatar
                  tile
                  size="120"
                  v-if="
                    campaign.mediaType == 'image/jpg' ||
                      campaign.mediaType == 'image/jpeg' ||
                      campaign.mediaType == 'image/png' ||
                      campaign.mediaType == 'image/gif'
                  "
                >
                  <img
                    :src="loadImage(campaign.mediaType, campaign.campaignMedia)"
                    :alt="loadImage(campaign.mediaType, campaign.campaignMedia)"
                  />
                </v-list-item-avatar>
                <v-list-item-avatar tile size="120" v-else>
                  <video width="600" height="300" controls duration="5" muted>
                    <source
                      :src="
                        loadImage(campaign.mediaType, campaign.campaignMedia)
                      "
                    />
                    Your browser does not support the video tag.
                  </video>
                </v-list-item-avatar>
                <v-list-item-content>
                  <div class="display-1 font-weight-bold mb-4">
                    <span class="float-left">{{ campaign.campaignName }}</span>
                    <v-btn
                      small
                      class="float-right text-capitalize"
                      color="primary"
                    >
                      Pending Approval...
                    </v-btn>
                  </div>

                  <v-list-item-subtitle>
                    <v-row>
                      <v-col md="2">
                        Created <br /><br />
                        {{ campaign.created }}
                      </v-col>
                      <v-col md="2">
                        No. Screens<br /><br />
                        {{ campaign.numberOfScreens }}
                      </v-col>
                      <v-col md="2">
                        Deployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "deployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Undeployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "undeployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Budget <br /><br />
                        {{
                          createCurrencySymbol(campaign.campaignCountry) +
                            "" +
                            campaign.campaignCost
                        }}
                      </v-col>
                      <v-col md="2">
                        Delivered budget <br /><br />
                        {{ calculateUsedBudget(campaign.campaignDisplays) }}
                      </v-col>
                    </v-row>
                  </v-list-item-subtitle>
                </v-list-item-content>
                <v-divider vertical class="pl-4" />

                <v-list-item-action class="headline mb-1">
                  <v-btn icon disabled>
                    <v-icon>mdi-television-stop</v-icon>
                  </v-btn>
                  <v-btn icon disabled>
                    <v-icon>mdi-content-copy</v-icon>
                  </v-btn>
                  <v-tooltip right>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        icon
                        v-bind="attrs"
                        v-on="on"
                        v-on:click="createCurrentCampaign(campaign)"
                      >
                        <v-icon>mdi-delete</v-icon>
                      </v-btn>
                    </template>
                    <span>Delete Campaign</span>
                  </v-tooltip>
                </v-list-item-action>
              </v-list-item>
            </v-card>
          </transition>
          <transition
            name="fade"
            v-for="(campaign, index) in not_paid_campaigns"
            :key="index"
          >
            <v-card
              v-if="activeCampaign"
              class="mx-auto"
              min-width="344"
              outlined
              transition="fade"
            >
              <v-list-item three-line>
                <v-list-item-avatar
                  tile
                  size="120"
                  v-if="
                    campaign.mediaType == 'image/jpg' ||
                      campaign.mediaType == 'image/jpeg' ||
                      campaign.mediaType == 'image/png' ||
                      campaign.mediaType == 'image/gif'
                  "
                >
                  <img
                    :src="loadImage(campaign.mediaType, campaign.campaignMedia)"
                    :alt="loadImage(campaign.mediaType, campaign.campaignMedia)"
                  />
                </v-list-item-avatar>
                <v-list-item-avatar tile size="120" v-else>
                  <video width="600" height="300" controls duration="5" muted>
                    <source
                      :src="
                        loadImage(campaign.mediaType, campaign.campaignMedia)
                      "
                    />
                    Your browser does not support the video tag.
                  </video>
                </v-list-item-avatar>

                <v-list-item-content>
                  <div class="display-1 font-weight-bold mb-4">
                    <span class="float-left">{{ campaign.campaignName }}</span>
                    <v-btn
                      small
                      class="float-right text-capitalize"
                      color="primary"
                    >
                      Make Payment
                    </v-btn>
                  </div>

                  <v-list-item-subtitle>
                    <v-row>
                      <v-col md="2">
                        Created <br /><br />
                        {{ campaign.created }}
                      </v-col>
                      <v-col md="2">
                        No. Screens<br /><br />
                        {{ campaign.numberOfScreens }}
                      </v-col>
                      <v-col md="2">
                        Deployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "deployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Undeployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "undeployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Budget <br /><br />
                        {{
                          createCurrencySymbol(campaign.campaignCountry) +
                            "" +
                            campaign.campaignCost
                        }}
                      </v-col>
                      <v-col md="2">
                        Delivered budget <br /><br />
                        {{ calculateUsedBudget(campaign.campaignDisplays) }}
                      </v-col>
                    </v-row>
                  </v-list-item-subtitle>
                </v-list-item-content>
                <v-divider vertical class="pl-4" />

                <v-list-item-action class="headline mb-1">
                  <v-tooltip right>
                    <template v-slot:activator="{ attrs }">
                      <v-btn
                        icon
                        v-bind="attrs"
                        v-on:click="createArchive(campaign)"
                        title="Archive This Campaign"
                      >
                        <v-icon>mdi-archive</v-icon>
                      </v-btn>
                    </template>
                    <span>Archive this campaign</span>
                  </v-tooltip>
                  <v-btn icon disabled>
                    <v-icon>mdi-content-copy</v-icon>
                  </v-btn>
                  <v-btn icon v-on:click="createCurrentCampaign(campaign)">
                    <v-icon>mdi-delete</v-icon>
                  </v-btn>
                </v-list-item-action>
              </v-list-item>
            </v-card>
          </transition>
          <transition
            name="fade"
            v-for="(campaign, index) in activeCampaigns"
            :key="index"
          >
            <v-card
              v-if="activeCampaign"
              class="mx-auto"
              min-width="344"
              outlined
              transition="fade"
            >
              <v-list-item three-line>
                <v-list-item-avatar
                  tile
                  size="120"
                  v-if="
                    campaign.mediaType == 'image/jpg' ||
                      campaign.mediaType == 'image/jpeg' ||
                      campaign.mediaType == 'image/png' ||
                      campaign.mediaType == 'image/gif'
                  "
                >
                  <img
                    :src="loadImage(campaign.mediaType, campaign.campaignMedia)"
                    :alt="loadImage(campaign.mediaType, campaign.campaignMedia)"
                  />
                </v-list-item-avatar>
                <v-list-item-avatar tile size="120" v-else>
                  <video width="600" height="300" controls duration="5" muted>
                    <source
                      :src="
                        loadImage(campaign.mediaType, campaign.campaignMedia)
                      "
                    />
                    Your browser does not support the video tag.
                  </video>
                </v-list-item-avatar>

                <v-list-item-content>
                  <div class="display-1 font-weight-bold mb-4">
                    <span class="float-left">{{ campaign.campaignName }}</span>
                    <v-btn
                      small
                      class="float-right text-capitalize"
                      color="primary"
                      @click="dialog = true"
                    >
                      Edit File
                    </v-btn>
                  </div>

                  <v-list-item-subtitle>
                    <v-row>
                      <v-col md="2">
                        Created <br /><br />
                        {{ campaign.created }}
                      </v-col>
                      <v-col md="2">
                        No. Screens<br /><br />
                        {{ campaign.numberOfScreens }}
                      </v-col>
                      <v-col md="2">
                        Deployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "deployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Undeployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "undeployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Budget <br /><br />
                        {{
                          createCurrencySymbol(campaign.campaignCountry) +
                            "" +
                            campaign.campaignCost
                        }}
                      </v-col>
                      <v-col md="2">
                        Delivered budget <br /><br />
                        {{ calculateUsedBudget(campaign.campaignDisplays) }}
                      </v-col>
                    </v-row>
                  </v-list-item-subtitle>
                </v-list-item-content>
                <v-divider vertical class="pl-4" />

                <v-list-item-action class="headline mb-1">
                  <v-btn icon>
                    <v-icon>mdi-content-copy</v-icon>
                  </v-btn>
                  <v-tooltip right>
                    <template v-slot:activator="{ attrs }">
                      <v-btn
                        icon
                        v-bind="attrs"
                        v-on:click="createArchive(campaign)"
                        title="Archive This Campaign"
                      >
                        <v-icon>mdi-archive</v-icon>
                      </v-btn>
                    </template>
                    <span>Campaign Archive</span>
                  </v-tooltip>
                  <v-btn icon>
                    <v-icon v-on:click="createCurrentCampaign(campaign)"
                      >mdi-delete</v-icon
                    >
                  </v-btn>
                </v-list-item-action>
              </v-list-item>
            </v-card>
          </transition>
        </v-col>
      </v-row>
    </div>

    <div class="mt-12">
      <v-row justify="center">
        <v-col cols="12" md="12" class="mx-auto">
          <div @click="archivedCampaign = !archivedCampaign">
            <div class="display-1 font-weight-bold float-left cursor">
              Archived campaigns
              <v-icon v-if="archivedCampaign">
                mdi mdi-chevron-up
              </v-icon>
              <v-icon v-else>
                mdi mdi-chevron-down
              </v-icon>
            </div>
            <div class="float-right body-2">
              {{ archivedCampaigns.length }} archived campaigns
            </div>
          </div>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col
          v-for="(campaign, index) in archivedCampaigns"
          :key="index"
          md="12"
        >
          <transition name="fade">
            <v-card
              v-if="archivedCampaign"
              class="mx-auto"
              min-width="344"
              outlined
              transition="fade"
            >
              <v-list-item three-line>
                <v-list-item-avatar
                  tile
                  size="120"
                  v-if="
                    campaign.mediaType == 'image/jpg' ||
                      campaign.mediaType == 'image/jpeg' ||
                      campaign.mediaType == 'image/png' ||
                      campaign.mediaType == 'image/gif'
                  "
                >
                  <img
                    :src="loadImage(campaign.mediaType, campaign.campaignMedia)"
                    :alt="loadImage(campaign.mediaType, campaign.campaignMedia)"
                  />
                </v-list-item-avatar>
                <v-list-item-avatar tile size="120" v-else>
                  <video width="600" height="300" controls duration="5" muted>
                    <source
                      :src="
                        loadImage(campaign.mediaType, campaign.campaignMedia)
                      "
                    />
                    Your browser does not support the video tag.
                  </video>
                </v-list-item-avatar>

                <v-list-item-content>
                  <div class="display-1 font-weight-bold mb-4">
                    <span class="float-left">{{ campaign.campaignName }}</span>
                    <v-btn
                      small
                      class="float-right text-capitalize"
                      color="primary"
                      v-on:click="launch_archive_campaign(campaign)"
                    >
                      Activate
                    </v-btn>
                  </div>

                  <v-list-item-subtitle>
                    <v-row>
                      <v-col md="2">
                        Created <br /><br />
                        {{ campaign.created }}
                      </v-col>
                      <v-col md="2">
                        No. Screens<br /><br />
                        {{ campaign.numberOfScreens }}
                      </v-col>
                      <v-col md="2">
                        Deployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "deployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Undeployed Screens<br /><br />
                        {{
                          calculateDeployedScreens(
                            campaign.campaignDisplays,
                            "undeployed"
                          )
                        }}
                      </v-col>
                      <v-col md="2">
                        Budget <br /><br />
                        {{
                          createCurrencySymbol(campaign.campaignCountry) +
                            "" +
                            campaign.campaignCost
                        }}
                      </v-col>
                      <v-col md="2">
                        Delivered budget <br /><br />
                        {{ calculateUsedBudget(campaign.campaignDisplays) }}
                      </v-col>
                    </v-row>
                  </v-list-item-subtitle>
                </v-list-item-content>
                <v-divider vertical class="pl-4" />

                <v-list-item-action class="headline mb-1">
                  <v-btn icon disabled>
                    <v-icon>mdi-television-stop</v-icon>
                  </v-btn>
                  <v-btn icon disabled>
                    <v-icon>mdi-content-copy</v-icon>
                  </v-btn>
                  <v-tooltip right>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        icon
                        v-bind="attrs"
                        v-on="on"
                        disabled
                        title="Archived Campaign"
                      >
                        <v-icon v-on:click="createCurrentCampaign(campaign)"
                          >mdi-delete</v-icon
                        >
                      </v-btn>
                    </template>
                    <span>Campaign Archive</span>
                  </v-tooltip>
                </v-list-item-action>
              </v-list-item>
            </v-card>
          </transition>
        </v-col>
      </v-row>
    </div>

    <!--Dialogues -->
    <!--delete dialogue-->
    <v-dialog
      v-model="deleteModal"
      transition="dialog-top-transition"
      max-width="400"
    >
      <v-card max-width="400">
        <v-toolbar color="primary" dark>
          <v-icon>mdi mdi-warning </v-icon> {{ modalText.headerText }}
          <v-btn
            icon
            absolute
            right
            outlined
            class="float-right"
            color="white"
            @click="close_delete_modal()"
          >
            <v-icon>mdi mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <p class="pt-4 text-center text-h3">
            {{ modalText.questionText }} <br />
          </p>
        </v-card-text>
        <v-card-actions class="justify-center">
          <v-btn color="primary" @click="deleteCampaign()">
            {{ modalText.actionText }} <v-icon>mdi-check-outline</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!------------------>
    <v-row justify="center">
      <!--error Dialogue-->
      <v-dialog
        v-model="errorModal"
        transition="dialog-top-transition"
        max-width="400"
      >
        <v-card max-width="400">
          <v-toolbar color="primary" dark>
            <v-icon>mdi mdi-warning </v-icon> {{ modalText.headerText }}
            <v-btn
              icon
              absolute
              right
              outlined
              class="float-right"
              color="white"
              @click="errorModal = false"
            >
              <v-icon>mdi mdi-close</v-icon>
            </v-btn>
          </v-toolbar>
          <v-card-text>
            <p class="pt-4 text-center text-h3">
              {{ modalText.questionText }} <br />
            </p>
          </v-card-text>
        </v-card>
      </v-dialog>
      <v-dialog
        v-model="dialog"
        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
      >
        <v-card>
          <form enctype="multipart/form-data" @submit.prevent="piDeploy">
            <v-toolbar dark color="primary">
              <v-toolbar-title>Create a new campaign</v-toolbar-title>
              <v-spacer />
              <v-toolbar-items>
                <v-btn icon dark @click="dialog = false">
                  <v-icon>mdi-close</v-icon>
                </v-btn>
              </v-toolbar-items>
            </v-toolbar>
            <v-row justify="center">
              <v-col md="4">
                <v-stepper v-model="e1">
                  <v-stepper-header>
                    <v-stepper-step :complete="e1 > 1" step="1">
                      Campaign information
                    </v-stepper-step>

                    <v-divider />

                    <v-stepper-step :complete="e1 > 2" step="2">
                      Summary
                    </v-stepper-step>
                  </v-stepper-header>

                  <v-stepper-items>
                    <v-stepper-content step="1">
                      <div>
                        <p class="display-1 font-weight-bold">
                          Please enter your campaign details
                        </p>
                        <p class="primary--text cursor" @click="more = true">
                          More information about campaigns
                        </p>
                        <v-dialog v-model="more" width="500">
                          <v-card>
                            <v-card-title class="headline">
                              More information about campaigns
                              <v-btn icon absolute right @click="more = false">
                                <v-icon>mdi-close</v-icon>
                              </v-btn>
                            </v-card-title>
                            <v-divider />
                            <v-card-text
                              class="black--text text-justify pt-2 text-32"
                            >
                              A campaign is a self-contained advertising
                              campaign on selected displays in the Digit-Ad
                              network. A campaign consists of a name, a period,
                              a selection of displays, a clip and a targeted
                              capacity. At the bottom of the screen you can see
                              an overview of the parameters of the campaign and
                              how your selection affects the costs and the
                              payouts. Every single time your clip has been
                              played in full during the campaign period counts
                              as a playout. Since the displays and their running
                              time are limited, we reserve a selection made for
                              half an hour. The countdown below shows you how
                              long your selected displays will be available
                              until you should have booked them. If you have any
                              questions about booking campaigns, you can always
                              send an email to hello@digit-ad-domain.tech .
                            </v-card-text>
                          </v-card>
                        </v-dialog>
                      </div>
                      <v-divider class="my-5 py-1" />
                      <div>
                        <v-row>
                          <v-col md="10">
                            <v-text-field
                              dense
                              label="Campaign Name"
                              placeholder="Placeholder"
                              outlined
                              v-model="campaignInfo.campaignName"
                            />
                          </v-col>
                          <v-col md="2" class="mt-2">
                            <v-tooltip right>
                              <template v-slot:activator="{ on, attrs }">
                                <v-icon
                                  v-bind="attrs"
                                  class="float-left"
                                  v-on="on"
                                >
                                  mdi-alert-circle-outline
                                </v-icon>
                              </template>
                              <span>
                                Choose a unique name by which you can recognize
                                your campaign if you run multiple campaigns at
                                the same time
                              </span>
                            </v-tooltip>

                            <v-icon color="green" class="float-right">
                              mdi-check-bold
                            </v-icon>
                          </v-col>
                        </v-row>
                      </div>
                      <!---->
                      <div>
                        <v-row>
                          <v-col md="10">
                            <v-text-field
                              dense
                              label="Number of days"
                              placeholder="Placeholder"
                              outlined
                              v-model="campaignInfo.numberOfDays"
                            />
                          </v-col>
                          <v-col md="2" class="mt-2">
                            <v-tooltip right>
                              <template v-slot:activator="{ on, attrs }">
                                <v-icon
                                  v-bind="attrs"
                                  class="float-left"
                                  v-on="on"
                                >
                                  mdi-alert-circle-outline
                                </v-icon>
                              </template>
                              <span>
                                Enter the number of days you want the campaign
                                to run
                              </span>
                            </v-tooltip>

                            <v-icon color="green" class="float-right">
                              mdi-check-bold
                            </v-icon>
                          </v-col>
                        </v-row>
                      </div>
                      <!---->
                      <v-divider class="my-5 py-1" />
                      <div>
                        <v-row>
                          <v-col md="10">
                            <v-select
                              :items="countries"
                              label="Select Country"
                              outlined
                              dense
                              v-model="campaignInfo.campaignCountry"
                              v-on:change="getScreens()"
                            />
                          </v-col>
                          <v-col md="2" class="mt-2">
                            <v-tooltip right>
                              <template v-slot:activator="{ on, attrs }">
                                <v-icon
                                  v-bind="attrs"
                                  class="float-left"
                                  v-on="on"
                                >
                                  mdi-map
                                </v-icon>
                              </template>
                              <span>
                                Select your country
                              </span>
                            </v-tooltip>
                            <v-icon color="green" class="float-right">
                              mdi-check-bold
                            </v-icon>
                          </v-col>
                        </v-row>
                      </div>
                      <v-divider class="my-5 py-1" />
                      <div>
                        <v-row>
                          <v-col md="10">
                            <v-btn
                              color="primary"
                              block
                              class="text-capitalize font-weight-bold"
