<script>
import $ from "jquery";
import axios from "axios";
import Map from "./map";
import { mapGetters } from "vuex";
let piCredentials = {
  username: "pidigitadsignage",
  password: "Nelson@Akem@1000.",
};
let digitadApps = {
  users: "https://app.digitadooh.com",
  piApp: "//ourcore.digitadooh.com:3000",
};
export default {
  name: "DashboardView",
  components: {
    Map,
  },
  methods: {
    calculateUsedBudget(screens) {
      let usedBudget = 0;
      screens.forEach((screen) => {
        let exhaustedAmount = parseInt(screen.usedBudget);
        usedBudget = usedBudget + exhaustedAmount;
      });
      return usedBudget;
    },
    calculateDeployedScreens(screens, type) {
      let deployed = 0;
      screens.forEach((screen) => {
        if (parseInt(screen.deployed) == 1) {
          deployed = deployed + 1;
        }
      });
      if (type == "deployed") {
        return deployed;
      } else {
        //return undeployed
        let undeployed = screens.length - deployed;
        return undeployed;
      }
    },
    close_delete_modal() {
      this.deleteModal = false;
      //reload page
      location.reload();
    },
    reloadPage() {
      this.UploadDialogue = false;
      //reload page
      location.reload();
    },
    setDate() {
      this.return_date_array = this.campaignInfo.campaignDate;
    },
    deleteCampaign() {
      let campaignToDelete = this.currentCampaign;
      let user_id = this.userDetails.user_id;
      let campaign_name = campaignToDelete.campaignMedia;
      var user = piCredentials.username;
      var password = piCredentials.password;
      var base64encodedData = Buffer.from(user + ":" + password).toString(
        "base64"
      );
      //delete file from pisignage
      var config = {
        method: "delete",
        url: digitadApps.piApp + "/api/files/" + campaign_name,
        headers: {
          "Content-Type": "application/json",
          Authorization: `Basic ${base64encodedData}`,
        },
      };

      axios(config)
        .then(function(response) {
          console.log(JSON.stringify(response.data));
          if (response.data.success == true) {
            //update users platform
            let formData = new FormData();
            let data_param = JSON.stringify(campaignToDelete);
            formData.append("data", data_param);
            formData.append("user_id", user_id);
            const api = axios.create({ baseURL: digitadApps.users });
            api
              .post(digitadApps.users + "/server/deleteCampaign.php", formData)
              .then((result) => {
                console.log(result.data);
                if (result.data == 1) {
                  //campaign delete
                  location.reload();
                } else {
                  alert("Failed to delete!");
                }
              })
              .catch((error) => {
                console.log(error);
              });
          } else {
            alert("Failed to delete campaign");
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    createCurrentCampaign(campaign) {
      //current campain to delete
      (this.modalText.headerText = "Delete Campaign"),
        (this.modalText.actionText = "Delete"),
        (this.modalText.questionText =
          "Are you sure you want to delete this campaign!");
      this.currentCampaign = campaign;
      this.deleteModal = true;
    },
    videoData() {
      this.errors = [];
      let video = this.$refs.video;
      let videoDuration = video.duration;
      this.campaignInfo.duration = videoDuration;
      if (videoDuration > 10) {
        this.errors.push({
          value: "MAXIMUM VIDEO DURATION SHOULD BE 20 SECS.",
        });
        this.handleErrors();
      } else {
        this.errors = [];
      }
    },
    handleErrors() {
      if (this.errors.length > 0) {
        let initial_error = this.errors[0];
        this.modalText.questionText = initial_error.value;
        this.errorModal = true;
      }
    },
    launch_archive_campaign(campaignX) {
      //check if campaign is not active
      if (this.activeCampaigns.indexOf(campaignX) == -1) {
        let user_id = this.userDetails.user_id;
        //send campaign for archiving
        let formData = new FormData();
        let data_param = JSON.stringify(campaignX);
        formData.append("data", data_param);
        formData.append("user_id", user_id);
        const api = axios.create({
          baseURL: digitadApps.users,
        });
        api
          .post(
            digitadApps.users + "/server/lauch_archive_campaign.php",
            formData
          )
          .then((result) => {
            console.log(result.data);
            if (result.data == 1) {
              //reload page
              location.reload();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    loadImage(type, url) {
      return digitadApps.piApp + "/media/" + url;
    },
    createArchive(campaign) {
      //campaign to archive
      let campaignToArchive = campaign;
      //push to archive if not archived
      // console.log(this.archivedCampaigns.indexOf(campaignToArchive));
      if (this.archivedCampaigns.indexOf(campaignToArchive) == -1) {
        let user_id = this.userDetails.user_id;
        //send campaign for archiving
        let formData = new FormData();
        let data_param = JSON.stringify(campaignToArchive);
        formData.append("data", data_param);
        formData.append("user_id", user_id);
        const api = axios.create({
          baseURL: digitadApps.users,
        });
        api
          .post(digitadApps.users + "/server/createArchive.php", formData)
          .then((result) => {
            console.log(result.data);
            if (result.data == 1) {
              //push into archive store
              //reload page
              location.reload();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    getImage(url) {
      //console.log(url);
      return digitadApps.users + "/server/img/" + url; //require("http://digitad.us-east-2.elasticbeanstalk.com/img/campaign_img" + url);
    },
    piDeploy() {
      this.dialog = false;
      let campaign_upload_result = this.campaign_upload_result;
      let showLoader = this.showLoader;
      const options = {
        onUploadProgress: (ProgressEvent) => {
          const { loaded, total } = ProgressEvent;
          let percent = Math.floor((loaded * 100) / total);
          this.loaderWidth = percent;
        },
      };
      let user_id = this.userDetails.user_id;
      let userName = this.userDetails.f_name;
      let campaignOBJ = this.campaignInfo;
      let file = this.campaignInfo.campaignMedia;
      //create form data object
      //this.activeCampaigns.push(this.campaignInfo);
      let formData = new FormData();
      formData.append("assets", file);
      this.UploadDialogue = true;
      var user = piCredentials.username;
      var password = piCredentials.password;
      var base64encodedData = Buffer.from(user + ":" + password).toString(
        "base64"
      );
      var config = {
        method: "post",
        url: digitadApps.piApp + "/api/files/",
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: `Basic ${base64encodedData}`,
        },
        data: formData,
      };
      if (this.errors.length == 0) {
        axios(config)
          .then(function(response) {
            console.log(JSON.stringify(response.data));
            if (response.data.success == true) {
              //console.log(response.data.data);this is data about file
              let file_name = response.data.data[0].name;
              let file_info = JSON.stringify({
                files: [
                  {
                    name: response.data.data[0].name,
                    type: response.data.data[0].type,
                    size: response.data.data[0].size,
                    owner_id: user_id,
                    owner_name: userName,
                  },
                ],
                categories: ["string"],
              });
              //console.log(file_info);
              //store file details in pisignage database
              var config2 = {
                method: "post",
                url: digitadApps.piApp + "/api/postupload/",
                headers: {
                  "Content-Type": "application/json",
                  Authorization: `Basic ${base64encodedData}`,
                },
                data: file_info,
              };

              axios(config2)
                .then(function(response2) {
                  console.log(JSON.stringify(response2.data));
                  if (response2.data.success == true) {
                    //store file details in database
                    let formData = new FormData();
                    let data_param = JSON.stringify(campaignOBJ);
                    formData.append("data", data_param);
                    formData.append("file", file_name);
                    formData.append("user_id", user_id);
                    formData.append("user_name", userName);
                    const api = axios.create({ baseURL: digitadApps.users });
                    api
                      .post(
                        digitadApps.users + "/server/submit_campaign.php",
                        formData,
                        options
                      )
                      .then((result) => {
                        console.log(result);
                        if (result.data == 1) {
                          //hide progress bar and display message
                          campaign_upload_result =
                            "Campaign Uploaded Successfully";
                          showLoader = false;
                        } else {
                          //hide progress bar and display message
                          this.campaign_upload_result =
                            "Campaign Failed To Upload";
                          this.showLoader = false;
                        }
                      })
                      .catch((error) => {
                        console.log(error);
                      });
                    ////////////////////
                  }
                })
                .catch(function(error) {
                  console.log(error);
                });
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        this.handleErrors();
      }
    },
    set_price() {
      this.campaignInfo.campaignCost =
        this.campaignInfo.multiplyConstant *
        parseInt(this.campaignInfo.numberOfDays);
      //set launched campaign currency
      this.campaignInfo.currency = this.currency;
      //console.log(this.campaignInfo.capaignCost);
    },
    sort_city() {
      let new_country_info = [];
      if (this.sort_data == "Default") {
        new_country_info = this.reservedCountryInfo;
      } else {
        this.reservedCountryInfo.forEach((stateInfo) => {
          if (stateInfo.city == this.sort_data) {
            new_country_info.push(stateInfo);
          }
        });
      }
      this.countryInfo = new_country_info;
    },
    sortState() {
      let new_country_info = [];
      if (this.sort_data == "Default") {
        new_country_info = this.reservedCountryInfo;
      } else {
        this.reservedCountryInfo.forEach((stateInfo) => {
          if (stateInfo.state == this.sort_data) {
            new_country_info.push(stateInfo);
          }
        });
      }
      this.countryInfo = new_country_info;
    },
    previewCampaignMedia() {
      let file = this.campaignInfo.campaignMedia;
      this.file = this.campaignInfo.campaignMedia;
      let fileType = file.type;
      if (
        fileType == "video/mp4" ||
        fileType == "video/webm" ||
        fileType == "video/ogg"
      ) {
        this.preview_campaign_media = true;
        this.previewMedia = URL.createObjectURL(this.file);
      }
    },
    validateFile() {
      this.errors = [];
      let file = this.campaignInfo.campaignMedia;
      //console.log(file);
      let fileSize = (file.size / (1024 * 1024)).toFixed(2);
      let fileType = file.type;
      let f_name = file.name;
      if (fileSize <= 10) {
        if (
          fileType == "video/mp4" ||
          fileType == "video/webm" ||
          fileType == "image/jpeg" ||
          fileType == "image/jpg" ||
          fileType == "image/png" ||
          fileType == "image/gif" ||
          fileType == "video/ogg"
        ) {
          this.campaignInfo.mediaType = fileType;
          this.campaignInfo.file_name = f_name;
          this.campaignInfo.fSize = fileSize;
        } else {
          this.errors.push({
            value: "INVALID FILE TYPE! ONLY :MP4,WEBM,JPG,PNG AND GIF ALLOWED",
          });
        }
      } else {
        this.errors.push({ value: "FILE TOO LARGE, MAX FILESIZE = 10MB" });
      }
      this.handleErrors();
    },
    calculateDays() {
      /*let campaignDateRange = this.campaignInfo.campaignDate;
      //console.log(campaignDateRange);
      this.return_date_array = campaignDateRange;
      let startDate = new Date(this.campaignInfo.campaignDate[0]);
      let formatedStartDate = startDate.toDateString();
      this.campaignInfo.startDate = formatedStartDate;
      let endDate = new Date(this.campaignInfo.campaignDate[1]);
      let formatedEndDate = endDate.toDateString();
      this.campaignInfo.endDate = formatedEndDate;
      let timeDifference = endDate.getTime() - startDate.getTime();
      let dateDifference = timeDifference / (1000 * 3600 * 24);
      this.campaignInfo.numberOfDays = dateDifference;*/
      this.set_price();
    },
    reloadMap() {
      this.mapKey = this.mapKey + 1;
      console.log("reloaded:" + this.mapKey);
    },
    reduce_price() {
      let k = 0;
      if (
        this.currentScreen.gdp > 0 &&
        this.currentScreen.gdp <= 1000 &&
        this.currentScreen.population > 0 &&
        this.currentScreen.population <= 100000
      ) {
        k = k + 1;
      } else if (
        this.currentScreen.gdp > 0 &&
        this.currentScreen.gdp <= 1000 &&
        this.currentScreen.population >= 100001 &&
        this.currentScreen.population <= 500000
      ) {
        k = k + 1.1;
      } else if (
        this.currentScreen.gdp > 0 &&
        this.currentScreen.gdp <= 1000 &&
        this.currentScreen.population >= 5000001 &&
        this.currentScreen.population <= 1000000
      ) {
        k = 1.15;
      } else if (
        this.currentScreen.gdp > 0 &&
        this.currentScreen.gdp <= 1000 &&
